// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import Vuex from 'vuex'
import VueResource from 'vue-resource'

Vue.use(Vuex)
Vue.use(VueResource)

const store = new Vuex.Store({
	state: {
		news: [{"url": "http://www.freebuf.com/news/123748.html", "intro": "\u4e00\u4efd\u7814\u7a76\u62a5\u544a\u663e\u793a\uff0c\u5728\u4eca\u5e74\u7684\u5730\u4e0b\u9ed1\u5e02\u5e02\u573a\u4e2d\uff0c\u5404\u5927\u533b\u9662\u7684\u88ab\u76d7\u533b\u7597\u6570\u636e\u552e\u4ef7\u6025\u5267\u4e0b\u8dcc\u3002", "time": "2016-12-29", "title": "\u9ed1\u5e02\u7684\u533b\u7597\u6570\u636e\u4ef7\u683c\u8dcc\u81f310\u7f8e\u5143\u4ee5\u4e0b\uff0c\u9ed1\u5ba2\u7eb7\u7eb7\u8f6c\u5411\u52d2\u7d22\u8f6f\u4ef6"}, {"url": "http://www.freebuf.com/fevents/123889.html", "intro": "\u4e00\u5e74\u4e00\u5ea6\uff0c\u4e0d\u5bb9\u9519\u8fc7\uff01", "time": "2016-12-29", "title": "FreeBuf 2017\u4e92\u8054\u7f51\u5b89\u5168\u521b\u65b0\u5927\u4f1a\u89c6\u9891&\u56fe\u6587\u76f4\u64ad(\u5df2\u7ed3\u675f)"}, {"url": "http://www.freebuf.com/special/123961.html", "intro": "\u65e0\u8bba\u4f60\u662f\u5f00\u53d1\u4eba\u5458\u3001\u5b89\u5168\u4e13\u5bb6\u3001\u5ba1\u8ba1\u4eba\u5458\u3001\u6216\u8005\u662f\u6e17\u900f\u6d4b\u8bd5\u4eba\u5458\uff0c\u4f60\u90fd\u53ef\u4ee5\u5229\u7528\u8fd9\u4e9b\u7f51\u7ad9\u6765\u63d0\u5347\u4f60\u7684\u9ed1\u5ba2\u6280\u672f\u3002", "time": "2016-12-30", "title": "\u5408\u6cd5\u7ec3\u4e60\u9ed1\u5ba2\u6280\u672f\uff1f\u8fd915\u4e2a\u7f51\u7ad9\u4e5f\u8bb8\u53ef\u4ee5\u5e2e\u5230\u4f60"}, {"url": "http://www.freebuf.com/sectool/124060.html", "intro": "\u672c\u6b21\u66f4\u65b0\u5927\u5e45\u5ea6\u63d0\u5347\u4e86Nmap\u5728\u8fdb\u884c\u9ad8\u9891\u7387\u3001\u5927\u89c4\u6a21\u626b\u63cf\u4efb\u52a1\u65f6\u7684\u6027\u80fd\u3002", "time": "2016-12-30", "title": "\u5b89\u5168\u626b\u63cf\u795e\u5668Nmap 7.40\u65b0\u7248\u53d1\u5e03\uff0c\u63d0\u5347\u5927\u89c4\u6a21\u626b\u63cf\u6548\u7387"}, {"url": "http://www.freebuf.com/articles/terminal/124018.html", "intro": "\u8fd9\u4e9b\u53ef\u7591\u94fe\u63a5\u6307\u5411\u7684\u6587\u4ef6\u5747\u4e3a\u540d\u79f0\u5177\u6709\u8bf1\u60d1\u6027\u3001\u56fe\u6807\u66b4\u9732\u7684\u8272\u60c5\u64ad\u653e\u5668\u7c7b\u6076\u610f\u8f6f\u4ef6", "time": "2016-12-30", "title": "\u79fb\u52a8\u5e73\u53f0\u6d41\u91cf\u9ed1\u4ea7\u7814\u7a76\uff1a\u8272\u60c5\u64ad\u653e\u5668\u7c7b\u6076\u610f\u8f6f\u4ef6\u4ea7\u4e1a\u94fe"}]
	},
	mutations: {
		getNews (state, data){
			console.log(data)
			state.news = data['data']
		}
	},
	actions: {
		getNews (context, cat){
			Vue.http({
				    url: '/api/v1/topics',
				    method: 'GET',
				    params: {
				    	'cat': cat.name
				    }}).then(res => res.json().then(json=>{context.commit('getNews', json)}))
		}
	}
})

new Vue({
  el: '#app',
  store,
  template: '<App/>',
  components: { App }
})



