<template>
  <div id="app">
    <h1>Vue News</h1>
    <news_wraper></news_wraper>
    <button @click='change'>click</button>
  </div>
</template>

<script>
import news_wraper from './components/new_wraper'

export default {
  name: 'app',
  methods: {
    change () {
      this.$store.dispatch('getNews', {'name': 'hacker'})
    }
  },
  components: { news_wraper }
}
</script>

<style>

</style>



